/* ============================================================
   OMEGA 4.0 ‚Äî FRONTEND ‚Üî BACKEND INTEGRATION PATCH
   ============================================================
   This creates:
     - API client wrapper
     - Error normalization
     - Workspace-scoped auth headers
     - Aegis Safe Mode awareness
     - Health check bootstrap
     - Typed request helpers
================================================================ */

import { create } from "zustand";

/* ============================================================
   1) OMEGA CONFIG ‚Äî Backend URL + Endpoints
============================================================ */

export const OMEGA_CONFIG = {
  backendUrl: process.env.NEXT_PUBLIC_OMEGA_BACKEND ?? "http://localhost:8000",
  endpoints: {
    health: "/api/health",
    agents: "/api/tenant-agents",
    clients: "/api/clients",
    messages: "/api/messages",
    events: "/api/calendar/events",
    automations: "/api/automations",
    authMe: "/api/auth/me",
    authRefresh: "/api/auth/refresh",
  },
};

// Stripe endpoints
export const stripeEndpoints = {
  paymentStatus: (bookingId: string) => `/api/stripe/payment-status/${bookingId}`,
};

// SMS endpoints  
export const smsEndpoints = {
  conversations: "/api/sms/conversations",
  sendMessage: "/api/sms/send",
};

// Booking endpoints
export const bookingEndpoints = {
  list: "/api/bookings",
  create: "/api/bookings",
  get: (id: string) => `/api/bookings/${id}`,
  update: (id: string) => `/api/bookings/${id}`,
  delete: (id: string) => `/api/bookings/${id}`,
};

/* ============================================================
   2) Zustand Store ‚Äî Safe Mode + Health Monitoring
============================================================ */

type OmegaState = {
  healthy: boolean;
  safeMode: boolean;
  lastError: string | null;
  setHealthy: (h: boolean) => void;
  setSafeMode: (s: boolean) => void;
  setLastError: (err: string | null) => void;
};

export const useOmega = create<OmegaState>((set) => ({
  healthy: true,
  safeMode: false,
  lastError: null,
  setHealthy: (h) => set({ healthy: h }),
  setSafeMode: (s) => set({ safeMode: s }),
  setLastError: (e) => set({ lastError: e }),
}));

/* ============================================================
   3) NORMALIZED ERROR SHAPE
============================================================ */

export function normalizeError(error: any): string {
  if (!error) return "Unknown error";

  if (typeof error === "string") return error;

  if (error.detail) return error.detail;

  if (error.message) return error.message;

  try {
    return JSON.stringify(error);
  } catch {
    return "Unparsable error response";
  }
}

/* ============================================================
   4) TOAST HELPER (Simple console-based for now)
============================================================ */

function showToast(title: string, description: string, variant: "default" | "destructive" | "warning" = "default") {
  // Simple console logging for now
  // Can be replaced with a proper toast library later
  const prefix = variant === "destructive" ? "‚ùå" : variant === "warning" ? "‚ö†Ô∏è" : "‚ÑπÔ∏è";
  console.log(`${prefix} ${title}: ${description}`);
  
  // If you have a toast component, uncomment and use it:
  // toast({ title, description, variant });
}

/* ============================================================
   5) REQUEST WRAPPER (GET, POST, PATCH, DELETE)
============================================================ */

async function request(method: string, path: string, body?: any) {
  const state = useOmega.getState();

  const url = `${OMEGA_CONFIG.backendUrl}${path}`;

  const headers: Record<string, string> = {
    "Content-Type": "application/json",
  };

  // NOTE: Replace this with real JWT once OMEGA Auth is added
  // For now, check localStorage for token
  if (typeof window !== "undefined") {
    const token = window.localStorage.getItem("omega_token");
    if (token) headers["Authorization"] = `Bearer ${token}`;
  }

  const init: RequestInit = {
    method,
    headers,
  };

  if (body) init.body = JSON.stringify(body);

  let res: Response;

  try {
    res = await fetch(url, init);
  } catch (e) {
    state.setHealthy(false);
    const msg = normalizeError(e);
    showToast("Network Error", msg, "destructive");
    throw new Error(msg);
  }

  // Safe mode signal from backend (503 Service Unavailable)
  if (res.status === 503) {
    state.setSafeMode(true);
    showToast(
      "System in Safe Mode",
      "OMEGA temporarily restricted to protect data integrity.",
      "warning"
    );
  }

  let json: any = null;
  try {
    const text = await res.text();
    if (text) {
      json = JSON.parse(text);
    }
  } catch (e) {
    json = null;
  }

  if (!res.ok) {
    const msg = normalizeError(json);
    state.setLastError(msg);
    showToast("API Error", msg, "destructive");
    throw new Error(msg);
  }

  state.setHealthy(true);
  return json;
}

/* ============================================================
   6) OMEGA CLIENT API
============================================================ */

export const omegaClient = {
  get: <T = any>(path: string): Promise<T> => request("GET", path),
  post: <T = any>(path: string, body: any): Promise<T> => request("POST", path, body),
  patch: <T = any>(path: string, body: any): Promise<T> => request("PATCH", path, body),
  delete: <T = any>(path: string): Promise<T> => request("DELETE", path),
};

/* ============================================================
   7) STARTUP HEALTH CHECK (frontend boot)
============================================================ */

export async function initOmegaFrontend() {
  try {
    const res = await omegaClient.get(OMEGA_CONFIG.endpoints.health);

    // If backend reports safe mode
    if (res && typeof res === "object" && "safe_mode" in res && (res as any).safe_mode) {
      useOmega.getState().setSafeMode(true);
    }

    useOmega.getState().setHealthy(true);
    return true;
  } catch (e) {
    useOmega.getState().setHealthy(false);
    console.error("OMEGA Frontend health check failed:", e);
    return false;
  }
}

